// import Dropdown from "react-bootstrap/Dropdown";
// import Button from "react-bootstrap/esm/Button";
import Button from '@material-ui/core/Button';
import React, { useState } from "react";
import "react-responsive-modal/styles.css";
import { Modal } from "react-responsive-modal";
import "../App.css";

function Hero() {
  const [open, setOpen] = useState(false);
  // const dispatch = useDispatch()

  const onOpenModal = () => setOpen(true);
  const onCloseModal = () => setOpen(false);

  return (
    
    <div className="hero">
      <Button variant="outlined"  style={{backgroundColor: '#054DB9', color: 'black'}} onClick={onOpenModal}>All</Button>
      <div>
        <Modal open={open} onClose={onCloseModal} center>
          <div className="mainContmod">
            <h3>Activities during which time is shown ?</h3>
            <div className="subContmod">
              <h4>All</h4>
              <form>
                <div className="select-radio">
                  <p>
                    Activities during class-time,study-time and play time are
                    shown.
                  </p>
                  {/* Radio selection properties have been deleted */}
                  <input type="radio"></input>
                </div>
                
                <hr />
                
              </form>
            </div>
            <div className="subContmod">
              <h4>Class-time only</h4>
              <form className="subsub">
                <div className="select-radio">
                  <p>
                    Only the activities during the times you scheduled as
                    class-time are shown.
                  </p>
                  <input type="radio"></input>
                  </div>
            
                  <hr />
                
              </form>
            </div>
            <div className="subContmod">
              <h4>Study-time-only</h4>
              <form>
                <div className="select-radio">
                  <p>
                    Only the activities during the times you scheduled as
                    study-time or when manually switched to study-mode from the
                    mode page are shown.
                  </p>
                  <input type="radio"></input>
                  </div>
                  <hr />
                
              </form>
            </div>
            <div className="subContmod">
              <h4>Free-time-only</h4>
              <form>
                <div className="select-radio">
                  <p>
                    Only the activities during the times you scheduled as
                    free-time or when manually switched to free-mode from the
                    mode page are shown.are shown.
                  </p>
                  <input type="radio"></input>
                </div>
              </form>
            </div>
          </div>
        </Modal>
      </div>
    </div>
  );
}

export default Hero;
