import React from "react";
import { Doughnut } from "react-chartjs-2";
import ProgressBar from "react-bootstrap/ProgressBar";
import Button from "react-bootstrap/Button";
import DevicesOtherIcon from "@material-ui/icons/DevicesOther";
import "../App.css";

const state = {
  labels: ["class-time", "study-time", "free-time"],
  datasets: [
    {
      label: "Time",
      backgroundColor: ["#6495ed", "#90EE90", "#f08080"],
      hoverBackgroundColor: ["#005A9C", "#00ff00", "#ff6347"],
      circumference :360,
      data: [100, 30, 60],
      borderWidth : 2,
      offset : -3
    }
  ],
};

export default class DataSection extends React.Component {
  render() {
    return (
      <div classsName="chart">
      <div className="charttable">
        
        <div className="child">
         <h1>All Screen Time</h1>
         <div className="doughnet">
          <Doughnut
            data={state}
            options={{
              title: {
                display: true,
                text: "Average time per month",
                fontSize: 20,
              },
              legend: {
                display: true,
                position : "bottom",
              },
            }}
          />
          </div>
          
        </div>
        <div class= "vl"></div>

        <div className="child1">
          <h1>Free time usage</h1>
          <h6>Used</h6>
          <ProgressBar now={10} max="50" />
          <br />
          <Button variant="secondary">Extend</Button>
        </div>

        <div class= "vl"></div>

        <div className="ch">
          <h1>By Devices</h1>
          <div class="clr">
            <DevicesOtherIcon />
            <h6>device name</h6>
            <h6>Used</h6>
          </div>
          <div>
            <DevicesOtherIcon />
            <h6>device name</h6>
            <h6>Used</h6>
          </div>
        </div>
      </div>
      </div>
    );
  }
}
