import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import { Avatar } from "@material-ui/core";
import Box from "@material-ui/core/Box";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import Button from "@material-ui/core/Button";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import "../App.css";

const useStyles = makeStyles({
  rightToolbar: {
    marginLeft: "auto",
    marginRight: -12,
  },
});

function Header() {
  const [value, setValue] = React.useState(null);

  const handleClick = (event) => {
    setValue(event.currentTarget);
  };

  const handleClose = () => {
    setValue(null);
  };

  const classes = useStyles();
  return (
    <AppBar className="Appbar" position="static">
      <Toolbar>
        <Avatar src="/broken-image.jpg" style={{ backgroundColor: "#022742" }}></Avatar>
        <Box m={1}></Box>
        <Typography variant="title"  style={{ marginRight:120 }} color="inherit">
          Aditya Prasad
        </Typography>

        <section className={classes.rightToolbar}>
          <Button className="devicebtn" variant="outlined" color="primary">
            + Add device
          </Button>

          <Button aria-controls="Header" aria-haspopup="true" onClick={handleClick} >
            <MoreVertIcon />
          </Button>
          <Menu id="Header" anchorEl={value} keepMounted open={Boolean(value)} onClose={handleClose}>
            <MenuItem onClick={handleClose}>Add device</MenuItem>
            <MenuItem onClick={handleClose}>Edit</MenuItem>
            <MenuItem onClick={handleClose}>Delete</MenuItem>
          </Menu>
        </section>
      </Toolbar>
    </AppBar>
  );
}

export default Header;